<script>
export default {
  props: {
    state: { type: Object, required: true },
  },
  methods: {
    linkGen(currentPage) {
      return {
        name: this.$route.name,
        query: { page: currentPage },
      }
    },
  },
}
</script>

<template>
  <div>
    <div v-if="state.loading" class="loading">Loading...</div>
    <div v-else>
      <slot />
    </div>
    <b-pagination-nav
      v-if="!state.loading"
      :link-gen="linkGen"
      :number-of-pages="state.pageInfo.num_pages"
      pills
      use-router
      class="fixed-bottom"
      align="center"
    >
    </b-pagination-nav>
  </div>
</template>
