<script>
import Layout from '@layouts/main.vue'
import PagedRecords from '@components/nav-pagination.vue'
import { mapActions } from 'vuex'

export default {
  page: {
    title: 'Name',
    meta: [{ name: 'description', content: 'The Name page.' }],
  },
  components: { Layout, PagedRecords },
  props: { state: { type: Object, required: true } },
  methods: {
    ...mapActions('name', ['toggle_marked']),
  },
}
</script>

<template>
  <Layout>
    <PagedRecords :state="state">
      <b-card
        v-for="item in state.records"
        :key="item.id"
        no-body
        class="card d-inline-flex text-center m-1"
        :class="{
          'text-white': item.marked,
          'bg-success': item.marked,
          'rounded-pill': item.san_cai_good,
          'border-primary': item.wu_ge_rank === 3,
          'border-danger': item.wu_ge_rank === 4,
        }"
      >
        <a
          v-b-tooltip.html
          class="p-2 name"
          :title="
            '<strong>' +
              item.pinyin +
              '<strong><br>五行:' +
              item.wuxing +
              '<br>三才:' +
              item.san_cai
          "
          @click="toggle_marked(item)"
          >{{ item.first_name + item.last_name }}</a
        >
      </b-card>
    </PagedRecords>
  </Layout>
</template>
